import React, { useState, useEffect } from "react";
import { forwardRef } from "react";
import Grid from "@material-ui/core/Grid";

import MaterialTable from "material-table";
import AddBox from "@material-ui/icons/AddBox";
import ArrowDownward from "@material-ui/icons/ArrowDownward";
import Check from "@material-ui/icons/Check";
import ChevronLeft from "@material-ui/icons/ChevronLeft";
import ChevronRight from "@material-ui/icons/ChevronRight";
import Clear from "@material-ui/icons/Clear";
import DeleteOutline from "@material-ui/icons/DeleteOutline";
import Edit from "@material-ui/icons/Edit";
import FilterList from "@material-ui/icons/FilterList";
import FirstPage from "@material-ui/icons/FirstPage";
import LastPage from "@material-ui/icons/LastPage";
import Remove from "@material-ui/icons/Remove";
import SaveAlt from "@material-ui/icons/SaveAlt";
import Search from "@material-ui/icons/Search";
import ViewColumn from "@material-ui/icons/ViewColumn";
import Alert from "@material-ui/lab/Alert";
import TextField from "@material-ui/core/TextField";
import DateFnsUtils from "@date-io/date-fns";
import {
	MuiPickersUtilsProvider,
	KeyboardDatePicker,
} from "@material-ui/pickers";
import Button from "@material-ui/core/Button";
import api from "../api/api";

import "./table.css";

const tableIcons = {
	Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
	Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
	Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
	Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
	DetailPanel: forwardRef((props, ref) => (
		<ChevronRight {...props} ref={ref} />
	)),
	Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
	Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
	Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
	FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
	LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
	NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
	PreviousPage: forwardRef((props, ref) => (
		<ChevronLeft {...props} ref={ref} />
	)),
	ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
	Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
	SortArrow: forwardRef((props, ref) => (
		<ArrowDownward {...props} ref={ref} />
	)),
	ThirdStateCheck: forwardRef((props, ref) => (
		<Remove {...props} ref={ref} />
	)),
	ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />),
};

// function validateEmail(email) {
// 	const re = /^((?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\]))$/;
// 	return re.test(String(email).toLowerCase());
// }

function Table() {
	var lookup = {
		Invoice: "Invoice",
		Payment: "Payment",
		Credit: "Credit",
		Memo: "Memo",
		Adjustment: "Adjustment",
	};
	var columns = [
		{ title: "Transaction ID", field: "transaction_id" },
		{ title: "Date", field: "date", type: "date" },
		{ title: "Account ID", field: "account_id" },
		{ title: "Ref 1", field: "ref_1" },
		{ title: "Ref 2", field: "ref_2" },
		{ title: "Account Name", field: "account_name" },
		{ title: "Type", field: "type", lookup: lookup },
		{ title: "Amount", field: "amount", type: "number" },
		{ title: "Currency", field: "currency" },
		{ title: "USD", field: "usd" },
		{ title: "Notes", field: "notes" },
		{ title: "Balance", field: "balance" },
	];
	const [data, setData] = useState([]); //table data

	//for error handling
	const [iserror, setIserror] = useState(false);
	const [errorMessages, setErrorMessages] = useState([]);
	const [accountId, setaccountId] = useState();
	const [startDate, setstartDate] = useState(null);
	const [endDate, setendDate] = useState(null);

	// useEffect(() => {
	// 	api.get("/transactions")
	// 		.then((res) => {
	// 			setData(res.data);
	// 		})
	// 		.catch((error) => {
	// 			console.log("Error");
	// 		});
	// }, []);

	function submitHandler() {
		api.post("/transactionsFilter", { accountId, startDate, endDate })
			.then((res) => {
				setData(res.data);
			})
			.catch((error) => {
				console.log("Error");
			});
	}
	const handleRowUpdate = (newData, oldData, resolve) => {
		//validation
		let errorList = [];
		// if (newData.site === "") {
		// 	errorList.push("Please enter site");
		// }
		// if (newData.ad_type === "") {
		// 	errorList.push("Please enter ad type");
		// }
		// if (newData.ad_costs === "") {
		// 	errorList.push("Please enter ad costs");
		// }
		// if (newData.contact_info === "") {
		// 	errorList.push("Please enter contact info");
		// }

		if (errorList.length < 1) {
			api.patch("/transactions/" + newData._id, newData)
				.then((res) => {
					const dataUpdate = [...data];
					const index = oldData.tableData._id;
					dataUpdate[index] = newData;
					setData([...dataUpdate]);
					resolve();
					window.location.reload(false);
					setIserror(false);
					setErrorMessages([]);
				})
				.catch((error) => {
					setErrorMessages(["Update failed! Server error"]);
					setIserror(true);
					resolve();
				});
		} else {
			setErrorMessages(errorList);
			setIserror(true);
			resolve();
		}
	};

	const handleRowAdd = (newData, resolve) => {
		//validation
		let errorList = [];
		// if (newData.site === undefined) {
		// 	errorList.push("Please enter site");
		// }
		// if (newData.ad_type === undefined) {
		// 	errorList.push("Please enter ad type");
		// }
		// if (newData.ad_costs === undefined) {
		// 	errorList.push("Please enter ad costs");
		// }
		// if (newData.contact_info === undefined) {
		// 	errorList.push("Please enter contact info");
		// }

		if (errorList.length < 1) {
			//no error
			api.post("/transactions", newData)
				.then((res) => {
					let dataToAdd = [...data];
					dataToAdd.push(newData);
					setData(dataToAdd);
					resolve();
					window.location.reload(false);
					setErrorMessages([]);
					setIserror(false);
				})
				.catch((error) => {
					setErrorMessages(["Cannot add data. Server error!"]);
					setIserror(true);
					resolve();
				});
		} else {
			setErrorMessages(errorList);
			setIserror(true);
			resolve();
		}
	};

	const handleRowDelete = (oldData, resolve) => {
		api.delete("/transactions/" + oldData._id)
			.then((res) => {
				const dataDelete = [...data];
				const index = oldData.tableData._id;
				var x = dataDelete.filter((item) => item._id !== index);
				//dataDelete.splice(index, 1);
				setData([...x]);
				resolve();
				window.location.reload(false);
			})
			.catch((error) => {
				setErrorMessages(["Delete failed! Server error"]);
				setIserror(true);
				resolve();
			});
	};

	return (
		<div className="table">
			<div className="account-id">
				<TextField
					required
					id="standard-required"
					label="Account ID"
					onChange={(e) => setaccountId(e.target.value)}
					value={accountId}
				/>

				<MuiPickersUtilsProvider utils={DateFnsUtils}>
					<KeyboardDatePicker
						disableToolbar
						variant="inline"
						format="MM/dd/yyyy"
						margin="normal"
						id="date-picker-inline"
						label="Start Date"
						value={startDate}
						onChange={(date) => setstartDate(date)}
						KeyboardButtonProps={{
							"aria-label": "change date",
						}}
					/>
					<KeyboardDatePicker
						disableToolbar
						variant="inline"
						format="MM/dd/yyyy"
						margin="normal"
						id="date-picker-inline"
						label="End Date"
						onChange={(date) => setendDate(date)}
						value={endDate}
						KeyboardButtonProps={{
							"aria-label": "change date",
						}}
					/>
				</MuiPickersUtilsProvider>
				<Button
					variant="contained"
					color="primary"
					href="#contained-buttons"
					type="submit"
					onClick={submitHandler}
				>
					Submit
				</Button>
			</div>

			<Grid container spacing={1}>
				{/* <Grid item xs={3}></Grid> */}
				<Grid item xs={12}>
					<div>
						{iserror && (
							<Alert severity="error">
								{errorMessages.map((msg, i) => {
									return <div key={i}>{msg}</div>;
								})}
							</Alert>
						)}
					</div>
					<MaterialTable
						title="Date View"
						columns={columns}
						data={data}
						icons={tableIcons}
						// editable={{
						// 	onRowUpdate: (newData, oldData) =>
						// 		new Promise((resolve) => {
						// 			handleRowUpdate(newData, oldData, resolve);
						// 		}),
						// 	onRowAdd: (newData) =>
						// 		new Promise((resolve) => {
						// 			handleRowAdd(newData, resolve);
						// 		}),
						// 	onRowDelete: (oldData) =>
						// 		new Promise((resolve) => {
						// 			handleRowDelete(oldData, resolve);
						// 		}),
						// }}
						options={{
							exportButton: true,
						}}
					/>
				</Grid>
				{/* <Grid item xs={3}></Grid> */}
			</Grid>
		</div>
	);
}

export default Table;
